#root{height:100%;}
.wrap{height:100%;}
.ant-layout-content.content{padding:50px 40px;}

.agora-config .ant-card-body p{overflow:hidden;}
.btns .ant-btn{margin-right:10px;}

.button-color-dust {
  background-color: #F5222D;
  border-color: #F5222D;
  
  &:hover, &:focus {
    background-color: #ff4d4f;
    border-color: #ff4d4f;
  }
  
  &:active, &.active {
    background-color: #cf1322;
    border-color: #cf1322;
  }
}
  
